<template>
  <!-- 任务详情查看，已办结 -->
  <div class="app-container not-completeList">
    <div class="common-title">{{ taskName }}</div>
    <div class="common-container">
      <c-table v-loading="loading" :headers="headers" :data="tableData" border>
        <template slot="column|btns" slot-scope="{ row }">
          <el-button
            v-if="row.taskProgress === '1'"
            size="small"
            type="blue"
            icon="custom-check-icon"
            @click="onJump(row)"
            >查看</el-button
          >
          <el-button
            v-if="row.taskProgress === '1'"
            size="small"
            type="red"
            icon="custom-repulse-icon"
            @click="onRepulse(row)"
            >打回</el-button
          >
          <span v-if="row.taskProgress !== '1'">-</span>
        </template>
        <template slot="column|bonus" slot-scope="{ row }">
          <div
            class="bonus-box"
            style="display:flex;justify-content:space-between;align-items:center"
          >
            <span>{{
              row.bonus
                ? formatAmountValue(row.bonus)
                : formatNullValue(row.bonus)
            }}</span>
            <el-button type="primary" size="mini" @click="onEdit(row)"
              >修改</el-button
            >
          </div>
        </template>
      </c-table>
    </div>
    <el-dialog
      title="奖金池额度修改为："
      :visible.sync="dialogVisible"
      width="30%"
    >
      <el-input
        :value="bonus"
        @input="bonusChange"
        :maxlength="9"
        placeholder="请输入内容"
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onUpdateBonus()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Button, Dialog } from 'element-ui';
import CTable from '@/components/common/CTable';
import {
  getTaskDetailList,
  repulseSchool,
  updateBonus
} from '../../api/taskDetail/index';
import { formatNullValue, formatAmountValue } from '../../plugins/vue-filters';
export default {
  name: 'NotCompleteList',
  components: { [Button.name]: Button, CTable, [Dialog.name]: Dialog },
  props: {},
  data() {
    const askProgressObj = {
      0: '未提交',
      1: '已提交',
      2: '已打回'
    };
    return {
      formatNullValue: formatNullValue,
      formatAmountValue: formatAmountValue,
      loading: true,
      headers: [
        {
          label: '序号',
          type: 'index',
          width: 80
        },
        {
          label: '校方名称',
          prop: 'schoolName'
        },
        {
          label: '奖金池额度',
          prop: 'bonus',
          colType: 'column',
          slotName: 'bonus'
        },
        {
          label: '当前进度',
          prop: 'taskProgress',
          formatter: row => {
            return askProgressObj[row.taskProgress];
          }
        },
        {
          label: '操作',
          prop: 'handle',
          colType: 'column',
          slotName: 'btns'
        }
      ],
      tableData: [],
      dialogVisible: false,
      bonus: 0,
      currentId: ''
    };
  },
  computed: {
    taskName() {
      // 任务名
      return this.$route.query.taskName;
    }
  },
  watch: {},
  created() {
    this.getTaskDetailList();
  },

  mounted() {},
  methods: {
    bonusChange(val) {
      if (val === '') {
        this.bonus = '';
        return;
      }
      if (/^[1-9]\d*$/.test(val)) {
        this.bonus = val;
      }
    },
    onUpdateBonus() {
      updateBonus({ id: this.currentId, bonus: this.bonus }).then(res => {
        if (res.status === 200) {
          this.dialogVisible = false;
          this.$message.success(res.message);
          this.getTaskDetailList();
        }
      });
    },
    onEdit(row) {
      console.log(row);
      if (row.taskProgress == 1) {
        //已提交
        this.$message.warning('该校已提交任务，请打回后修改奖金池额度！');
      } else {
        //未提交/已打回
        this.dialogVisible = true;
        this.bonus = row.bonus;
        this.currentId = row.id;
      }
    },
    /**
     * 获取任务详情-未办结列表数据
     */
    getTaskDetailList() {
      getTaskDetailList({ taskName: this.taskName })
        .then(({ data }) => {
          console.log(data);
          this.tableData = data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    /**
     * 打回
     */
    onRepulse(row) {
      this.$confirm('打回后，用户需重新提交！', '提醒', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        center: true,
        showClose: false
      }).then(() => {
        repulseSchool({ taskId: row.id }).then(() => {
          this.getTaskDetailList();
          this.$message({
            type: 'success',
            message: '操作成功!'
          });
        });
      });
    },
    /**
     * 查看
     */
    onJump(row) {
      this.$router.push({
        path: '/task-detial/single',
        query: {
          taskName: row.name,
          schoolName: row.schoolName,
          bonus: row.bonus
        }
      });
    }
  }
};
</script>

<style lang="less">
.not-completeList {
  .bonus-box {
    .el-button--primary {
      min-width: 0 !important;
      padding: 3px 8px !important;
    }
  }
}
</style>
