<template>
  <!-- 任务详情单条内容查看 -->
  <div class="app-container" v-loading="loading">
    <div class="school-info">
      <h1>{{ taskName }}</h1>
      <p>
        <span>校方：{{ schoolName }}</span>
        <span
          >奖金池额度：<b>{{
            bonus | formatNullValue | formatAmountValue
          }}</b></span
        >
      </p>
    </div>
    <div class="common-container container-wrap">
      <el-button
        type="primary"
        size="medium"
        class="btn-download"
        @click="onDownload()"
        >下载</el-button
      >
      <div class="school-main">
        <div class="main-left">
          <ranking-panel :data="tableData"></ranking-panel>
        </div>
        <div class="main-right">
          <div class="main-right-top">
            <match-panel :data="data"></match-panel>
          </div>
          <div class="main-right-bottom">
            <salary-panel :data="data"></salary-panel>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from 'element-ui';
import RankingPanel from './components/RankingPanel.vue';
import SalaryPanel from './components/SalaryPanel.vue';
import MatchPanel from './components/MatchPanel.vue';
import { taskDetailsOne, downloadCompleteList } from '../../api/taskDetail';
export default {
  name: 'SingleDetail',
  components: {
    [Button.name]: Button,
    RankingPanel,
    SalaryPanel,
    MatchPanel
  },
  props: {},
  data() {
    return {
      loading: true,
      // 表格数据
      tableData: [],
      // 取数组第一条数据，用来取匹配度和年薪系数
      data: {}
    };
  },
  computed: {
    taskName() {
      // 任务名
      return this.$route.query.taskName;
    },
    schoolName() {
      // 学校名
      return this.$route.query.schoolName;
    },
    bonus() {
      // 额度
      return Number(this.$route.query.bonus);
    }
  },
  watch: {},
  created() {
    this.taskDetailsOne();
  },
  mounted() {},
  methods: {
    /**
     * 获取单条详情数据
     */
    taskDetailsOne() {
      taskDetailsOne({
        taskName: this.taskName,
        schoolName: this.schoolName
      })
        .then(({ data }) => {
          this.tableData = data;
          console.log(data);
          this.data = data.length > 0 ? data[0] : {};
        })
        .finally(() => {
          this.loading = false;
        });
    },
    /**
     * 下载
     */
    onDownload() {
      let url = downloadCompleteList({
        taskName: this.taskName,
        schoolName: this.schoolName
      });
      window.open(url);
    }
  }
};
</script>

