<template>
  <!-- 参与角色选择 -->
  <el-dialog :visible.sync="visible" title="参与角色选择" center>
    <div class="checkbox-box">
      <el-checkbox
        :indeterminate="isIndeterminate"
        v-model="checkAll"
        @change="handleCheckAllChange"
        >全部</el-checkbox
      >
      <div style="margin: 15px 0;"></div>
      <el-checkbox-group
        v-model="checkedSchools"
        @change="onCheckedSchoolsChange"
      >
        <el-checkbox
          v-for="(item, index) in schoolList"
          :label="item.associatedAccount"
          :key="index"
          >{{ item.schoolName }}</el-checkbox
        >
      </el-checkbox-group>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button
        size="small"
        :loading="loading || dataLoading"
        @click="visible = false"
      >
        取消
      </el-button>
      <el-button
        size="small"
        :loading="loading || dataLoading"
        type="primary"
        @click="onConfirm"
      >
        确定
      </el-button>
    </span>
  </el-dialog>
</template>

<script>
import { Dialog, Button, Checkbox, CheckboxGroup } from 'element-ui';
import { getSchools } from '../../../api/task';
const cityOptions = ['上海', '北京', '广州', '深圳'];
export default {
  name: 'ChooseRole',
  components: {
    [Dialog.name]: Dialog,
    [Button.name]: Button,
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup
  },
  props: {
    taskName: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      visible: false, // 默认隐藏
      loading: false, // 数据提交
      dataLoading: false, // 数据加载
      checkAll: false, // 全选，默认不选
      checkedSchools: [], // 选中的学校
      allSchools: [], // 全部学校
      isIndeterminate: true,
      schoolList: [], // 可选学校
      checkedSchoolList: [] // 选中的学校
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    /**
     * 初始化数据
     */
    initData() {
      this.checkedSchools = []; // 选中的学校
      this.allSchools = []; // 全部学校
      this.schoolList = []; // 可选学校
      this.checkedSchoolList = []; // 选中的学校
    },
    /**
     * 获取学校数据
     */
    getSchools() {
      getSchools().then(({ data }) => {
        console.log(data);
        for (let i in data) {
          console.log(i, data[i]);
          this.schoolList.push({
            schoolName: i,
            associatedAccount: data[i]
          });
          this.allSchools.push(data[i]);
        }
      });
    },
    /**
     * 全选
     */
    handleCheckAllChange(val) {
      console.log(val);
      this.checkedSchools = val ? this.allSchools : [];
      this.isIndeterminate = false;
    },
    /**
     * 单选
     */
    onCheckedSchoolsChange(value) {
      console.log(value);
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.allSchools.length;
      console.log('checkAll', this.checkAll);
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.allSchools.length;
    },
    /**
     * 弹框显示
     */
    show() {
      this.initData();
      this.getSchools();
      this.visible = true;
    },
    /**
     * 弹框隐藏
     */
    hide() {
      this.visible = false;
    },
    /**
     * 获取选中学校
     */
    getSchoolList() {
      this.checkedSchoolList = this.schoolList
        .filter(
          item => this.checkedSchools.indexOf(item.associatedAccount) !== -1
        )
        .map(item => ({ ...item, taskName: this.taskName, bonus: null }));
    },
    /**
     * 提交角色
     */
    onConfirm() {
      this.hide();
      this.getSchoolList();
      this.$emit('checkedSchool', this.checkedSchoolList);
    }
  }
};
</script>

