<template>
  <!-- 任务详情查看，未办结 -->
  <div class="app-container">
    <div class="common-title">{{ taskName }}</div>
    <div class="common-container">
      <div class="common-header">
        <div class="common-header-left">
          <el-input
            v-model="schoolName"
            type="text"
            size="medium"
            placeholder="请输入要查询的学校名称"
            prefix-icon="el-icon-search"
            class="search-box"
          >
            <el-button
              type="primary"
              size="medium"
              slot="append"
              :loading="loading"
              @click="onSearchSchool"
              >查询</el-button
            >
          </el-input>
          <el-button
            type="primary"
            size="medium"
            class="common-btn"
            :loading="loading"
            @click="onExport"
            >导出</el-button
          >
          <el-button
            type="primary"
            size="medium"
            :loading="loading"
            @click="onDownload"
            >下载</el-button
          >
        </div>
      </div>
      <p class="common-tips">实际年薪内收入（已发年薪内+分配奖金）计</p>
      <c-table
        border
        ref="cTable"
        :headers="headers"
        :data="tableData"
        :highlight-current-row="true"
        v-loading="loading"
        @selection-change="onSelectionChange"
      >
        <template slot="column|link" slot-scope="{ row, $index }">
          <el-link type="primary" @click="onJump(row)">
            <span :ref="'school' + row.id" :data-index="$index">{{
              row.schoolName
            }}</span>
          </el-link>
        </template>
      </c-table>
    </div>
  </div>
</template>

<script>
import { Link, Input, Button } from 'element-ui';
import CTable from '@/components/common/CTable';
import { formatNullValue, formatAmountValue } from '../../plugins/vue-filters';
import {
  getTaskDetailList,
  exportCompleteList,
  batchDownloadCompleteList,
  searchSchool
} from '../../api/taskDetail';
export default {
  name: 'CompleteList',
  components: {
    [Link.name]: Link,
    [Input.name]: Input,
    [Button.name]: Button,
    CTable
  },
  props: {},
  data() {
    return {
      loading: true,
      headers: [
        {
          type: 'selection'
        },
        {
          label: '序号',
          type: 'index',
          width: 70,
          fixed: 'left'
        },
        {
          label: '校方名称',
          prop: 'schoolName',
          colType: 'column',
          slotName: 'link',
          fixed: 'left'
        },
        {
          label: '奖金池额度',
          prop: 'bonus',
          fixed: 'left',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前10% 匹配度',
          prop: 'matchedDegree10',
          fit: true
        },
        {
          label: '前10% max年薪',
          prop: 'max10',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前10% min年薪',
          prop: 'min10',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前10% ave年薪',
          prop: 'ave10',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前10% ave系数',
          prop: 'aveFactor10'
        },
        {
          label: '前20% 匹配度',
          prop: 'matchedDegree20'
        },
        {
          label: '前20% max年薪',
          prop: 'max20',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前20% min年薪',
          prop: 'min20',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前20% ave年薪',
          prop: 'ave20',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前20% ave系数',
          prop: 'aveFactor20'
        },
        {
          label: '前30% 匹配度',
          prop: 'matchedDegree30'
        },
        {
          label: '前30% max年薪',
          prop: 'max30',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前30% min年薪',
          prop: 'min30',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前30% ave年薪',
          prop: 'ave30',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '前30% ave系数',
          prop: 'aveFactor30'
        },
        {
          label: '后20% 匹配度',
          prop: 'matchedDegreeAfter20'
        },
        {
          label: '后20% max年薪',
          prop: 'maxAfter20',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '后20% min年薪',
          prop: 'minAfter20',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '后20% ave年薪',
          prop: 'aveAfter20',
          formatter: (row, col, val) => {
            return !val ? formatNullValue(val) : formatAmountValue(val);
          }
        },
        {
          label: '后20% ave系数',
          prop: 'aveFactorAfter20'
        }
      ],
      // 列表数据
      tableData: [],
      // 选中
      selectedRows: [],
      // 搜索学校名
      schoolName: ''
    };
  },
  computed: {
    // 任务名
    taskName() {
      return this.$route.query.taskName;
    },
    // 导出、下载参数
    params() {
      return {
        schoolNames: '',
        taskName: this.taskName
      };
    }
  },
  watch: {},
  created() {
    this.getTaskDetailList();
  },
  mounted() {},
  methods: {
    /**
     * 获取任务详情
     */
    getTaskDetailList() {
      getTaskDetailList({ taskName: this.taskName })
        .then(({ data }) => {
          this.tableData = data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    /**
     * 跳转详情
     */
    onJump(row) {
      this.$router.push({
        path: '/task-detial/single',
        query: {
          taskName: row.taskName,
          schoolName: row.schoolName,
          bonus: row.bonus
        }
      });
    },
    /**
     * 改变选择项
     */
    onSelectionChange(rows) {
      this.selectedRows = rows;
    },
    /**
     * 获取导出、下载请求数据
     */
    getParams() {
      let schoolNames = this.selectedRows.map(item => {
        return item.schoolName;
      });
      this.params.schoolNames = schoolNames.join(',');
    },
    /**
     * 导出当前勾选内容列表
     */
    onExport() {
      if (this.selectedRows.length === 0) {
        this.$alert('请选择任一条目进行操作！', '提醒', {
          cancelButtonText: '确定',
          center: true,
          showClose: false
        });
      } else {
        this.getParams();
        let url = exportCompleteList(this.params);
        window.open(url);
      }
    },
    /**
     * 批量下载任务详情
     */
    onDownload() {
      if (this.selectedRows.length === 0) {
        this.$alert('请选择任一条目进行操作！', '提醒', {
          cancelButtonText: '确定',
          center: true,
          showClose: false
        });
      } else {
        this.getParams();
        let url = batchDownloadCompleteList(this.params);
        window.open(url);
      }
    },
    /**
     * 搜索学校
     */
    onSearchSchool() {
      this.schoolName &&
        searchSchool({
          taskName: this.taskName,
          schoolName: this.schoolName
        })
          .then(({ data }) => {
            let searchDom = this.$refs['school' + data.id];
            let index = searchDom.getAttribute('data-index'); // 搜索下标
            // 滑动到对用搜索项
            searchDom.scrollIntoView({
              behavior: 'smooth',
              block: 'center',
              inline: 'center'
            });
            // 高亮
            this.$refs.cTable.$refs.table.setCurrentRow(this.tableData[index]);
          })
          .catch(err => {
            console.log(err);
            this.$message({
              message: err.message,
              type: 'warning'
            });
          });
    }
  }
};
</script>

<style lang="less" scoped>
/deep/ .el-input-group {
  width: 360px;
  margin-right: 60px;
  .el-input-group__append button.el-button {
    min-width: 68px;
    color: #fff;
    background: #0076f6;
    border-color: #0076f6;
  }
}
.search-box {
  .el-button {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .el-input-group__append {
    border-color: #0076f6;
  }
}
</style>
