import axios from '@/plugins/axios';
import qs from 'qs';
import API_HOST from '@/plugins/common/api';
import { getToken } from '@/plugins/core/auth';

/**
 * 管理端
 * 任务详情-未办结列表、未办结列表
 */
export function getTaskDetailList(params) {
  return axios(
    {
      url: '/task/TaskDetails',
      method: 'GET',
      params
    },
    'bonus-be'
  );
}

/**
 * 管理端
 * 任务详情-未办结
 * 打回
 */
export function repulseSchool(data) {
  console.log('repulseSchool', data);
  return axios(
    {
      url: '/task/repulseTask',
      method: 'POST',
      data: data
    },
    'bonus-be'
  );
}

