/**
 * 管理端
 * 任务详情-已办结列表
 * 导出
 */
export function exportCompleteList(params) {
  console.log('exportCompleteList', params);
  let url = API_HOST['bonus-be'] + '/task/exportForData';
  return url + '?' + qs.stringify(params);
}

