<template>
  <!-- 绩效排名 -->
  <c-table :headers="headers" :data="data" border :max-height="cHeight - 320">
    <template slot="column|ranking" slot-scope="{ row }">
      <span :class="{ red: row.warning == 1 ? true : false }">{{
        row.realizedIncomeRanking
      }}</span>
    </template>
  </c-table>
</template>

<script>
import CTable from '@/components/common/CTable';
export default {
  name: 'RankingPanel',
  components: { CTable },
  props: {
    /**
     * 获取表格数据
     */
    data: {
      type: Array
    }
  },
  data() {
    function formatterNum(value) {
      if (typeof value === 'number' && !isNaN(value)) {
        return value.toLocaleString();
      } else {
        return '-';
      }
    }
    return {
      headers: [
        {
          label: '绩效排名',
          prop: 'performanceRanking',
          width: 50
        },
        {
          label: '姓名',
          prop: 'teacherName'
        },
        {
          label: '工号',
          prop: 'teacherNum',
          width: 90
        },
        {
          label: '年薪',
          prop: 'salary',
          formatter: row => {
            return formatterNum(row.salary);
          }
        },
        {
          label: '已发年薪内收入',
          prop: 'plannedIncome',
          formatter: row => {
            return formatterNum(row.plannedIncome);
          }
        },
        {
          label: '已发年薪外收入',
          prop: 'unplannedIncome',
          formatter: row => {
            return formatterNum(row.unplannedIncome);
          }
        },
        {
          label: '分配奖金',
          prop: 'bonus',
          formatter: row => {
            return formatterNum(row.bonus);
          }
        },
        {
          label: '实际年薪内收入(已发年薪内+奖金)',
          width: 140,
          prop: 'realizedIncome',
          formatter: row => {
            return formatterNum(row.realizedIncome);
          }
        },
        {
          label: '实际年薪内收入排名',
          prop: 'realizedIncomeRanking',
          slotName: 'ranking',
          colType: 'column',
          width: 100
        },
        {
          label: '备注',
          prop: 'note',
          'show-overflow-tooltip': true
        }
      ]
    };
  },
  computed: {
    cHeight() {
      let h =
        document.documentElement.clientHeight || document.body.clientHeight; // 窗口高度
      let minH = 552 + 268; // 外层最小高度school-main + 头部底部高度
      return h > minH ? h : minH;
    }
  },
  watch: {},
  created() {
    console.log();
  },
  mounted() {},
  methods: {}
};
</script>

<style lang="less" scoped>
.m-table {
  height: 100%;
  /deep/ .el-table {
    height: 100%;
    max-height: 500px;
    overflow-x: auto;
  }
}
.red {
  color: #ff442a;
}
</style>
