<template>
  <!-- 新建任务 -->
  <div>
    <div class="app-container">
      <div class="common-title">{{ taskName }}</div>
      <div class="common-container">
        <!-- 头部 -->
        <div class="common-header">
          <div class="common-header-left">
            <el-button
              type="primary"
              size="medium"
              @click="onChooseRole"
              class="common-btn my-btn"
            >
              参与角色选择
            </el-button>
            <el-button
              type="primary"
              size="medium"
              @click="onRelease"
              :disabled="isReleased"
            >
              发布
            </el-button>
          </div>
          <div class="common-header-right"></div>
        </div>
        <div>
          <el-form
            v-loading="dataLoading"
            ref="elForm"
            :model="formData"
            size="mini"
          >
            <c-table :headers="headers" :data="formData.tableData" border>
              <template slot="column|bonus" slot-scope="{ row, $index }">
                <el-form-item
                  :prop="`tableData.${$index}.bonus`"
                  :key="`tableData.${$index}.bonus`"
                >
                  <el-input
                    v-model="row.bonus"
                    maxlength="8"
                    placeholder="手动输入"
                    :disabled="isReleased"
                    @blur="blurInput($event, row, $index)"
                    @focus="foucusInput($event, row, $index)"
                  ></el-input>
                </el-form-item>
              </template>
            </c-table>
          </el-form>
        </div>
      </div>
      <choose-role
        ref="chooseRole"
        :task-name="taskName"
        @checkedSchool="getCheckedSchool"
      ></choose-role>
    </div>
  </div>
</template>

<script>
import { Form, FormItem, Input } from 'element-ui';
import CTable from '@/components/common/CTable';
import ChooseRole from './components/ChooseRole.vue';
import { releaseTask } from '../../api/task/index';
export default {
  name: 'NewTask',
  components: {
    [Form.name]: Form,
    [FormItem.name]: FormItem,
    [Input.name]: Input,
    ChooseRole,
    CTable
  },
  props: {},
  data() {
    return {
      dataLoading: false, // 数据提交时loading
      formData: {}, // 提交数据
      isReleased: false, // 默认还未发布
      headers: [
        {
          label: '序号',
          type: 'index',
          width: 80
        },
        {
          label: '校方名称',
          prop: 'schoolName'
        },
        {
          label: '关联账号',
          prop: 'associatedAccount'
        },
        {
          label: '奖金池额度',
          prop: 'bonus',
          colType: 'column',
          slotName: 'bonus'
        }
      ]
    };
  },
  computed: {
    taskName() {
      return this.$route.query.taskName;
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {
    /**
     * 角色选择
     */
    onChooseRole() {
      if (!this.isReleased) {
        this.$refs.chooseRole.show();
      } else {
        this.$alert('已发布，不可重复操作！', '提醒', {
          cancelButtonText: '确定',
          center: true,
          showClose: false
        });
      }
    },
    /**
     * 获取学校
     */
    getCheckedSchool(val) {
      this.$set(this.formData, 'tableData', val);
    },
    /**
     * 发布
     */
    onRelease() {
      if (this.formData.tableData && this.formData.tableData.length > 0) {
        let reg = /^(0|[1-9][0-9]{0,8})$/;
        let data = [];
        let bool = this.formData.tableData.every(item => {
          if (
            item.bonus === null ||
            (typeof item.bonus === 'string' && item.bonus.length === 0)
          ) {
            this.$alert('奖金池额度不可为空！', '提醒', {
              cancelButtonText: '确定',
              center: true,
              showClose: false
            });
            return false;
          }
          let bonus = Number(this.delcommafy(item.bonus));
          if (!reg.test(bonus)) {
            this.$alert('奖金池额度需在0-99,999,999之间！', '提醒', {
              cancelButtonText: '确定',
              center: true,
              showClose: false
            });
            return false;
          }
          data.push({
            ...item,
            bonus: bonus
          });
          return true;
        });
        bool &&
          this.$confirm(
            '确定发布该任务至各单位？<br>发布后奖金池额度将无法修改',
            '提醒',
            {
              confirmButtonText: '确认',
              cancelButtonText: '取消',
              center: true,
              showClose: false,
              dangerouslyUseHTMLString: true
            }
          ).then(() => {
            this.dataLoading = true;
            releaseTask(data).then(() => {
              this.$message({
                type: 'success',
                message: '发布成功!'
              });
              this.dataLoading = false;
              this.isReleased = true;
            });
          });
      } else {
        this.$alert('请先选择参与角色！', '提醒', {
          cancelButtonText: '确定',
          center: true,
          showClose: false
        });
      }
    },
    /**
     * 金额添加千分位
     */
    comdify(n) {
      if (!n) return n;
      let str = n.split('.');
      let re = /\d{1,3}(?=(\d{3})+$)/g;
      let n1 = str[0].replace(re, '$&,');
      return str.length > 1 && str[1] ? `${n1}.${str[1]}` : `${n1}`;
    },
    /**
     * 金额删除千分位
     */
    delcommafy(num) {
      if (!num) return num;
      num = num.toString();
      num = num.replace(/,/gi, '');
      return num;
    },
    /**
     * 失焦input
     */
    blurInput(el, row, index) {
      let inputVal = el.target.value || '';
      this.formData.tableData[index].bonus = this.comdify(
        this.delcommafy(inputVal)
      );
    },
    /**
     * 聚焦input
     */
    foucusInput(el, row, index) {
      let inputVal = el.target.value || '';
      this.formData.tableData[index].bonus = this.delcommafy(inputVal);
    }
  }
};
</script>

<style lang="less" scoped>
// 去掉input边框线
/deep/.el-input__inner {
  border: 0;
}
// 去掉form-item的下边距
/deep/ .el-form-item--mini.el-form-item {
  margin-bottom: 0;
}
.my-btn {
  width: 118px;
}
</style>
