<!-- 任务列表 -->
<template>
  <div class="app-container">
    <div class="common-title">任务列表</div>
    <div class="common-container">
      <!-- 头部 -->
      <div class="common-header">
        <div class="common-header-left">
          <el-button
            type="primary"
            size="medium"
            :loading="loading"
            @click="onClickNewTask"
          >
            新增任务
          </el-button>
        </div>
        <div class="common-header-right"></div>
      </div>
      <div v-if="tableData.length > 0">
        <c-table
          v-loading="loading"
          :headers="headers"
          :data="tableData"
          border
        >
          <template slot="column|time" slot-scope="{ row }">
            {{ row.createTime | formatterTime }}
          </template>
          <template slot="column|link" slot-scope="{ row }">
            <el-link type="primary" @click="onJump(row)">{{
              row.taskName
            }}</el-link>
          </template>
          <template slot="column|btns" slot-scope="{ row }">
            <el-button
              class="btn-blue"
              v-if="row.status === '0'"
              type="primary"
              plain
              @click="onCompelete(row)"
              >办结</el-button
            >
            <span v-if="row.status === '1'" class="btn-red">已办结</span>
          </template>
        </c-table>
      </div>
      <div v-else class="empty">
        <img
          class="empty-task"
          src="../../assets/images/empty.png"
          alt=""
          srcset=""
        />
        <span>暂无数据~</span>
      </div>
    </div>
  </div>
</template>

<script>
import { Link, Button } from 'element-ui';
import CTable from '@/components/common/CTable';
import {
  manageGetTaskList,
  completeTask,
  isHaveTaskName
} from '../../api/task';
import { mapGetters } from 'vuex';

export default {
  neme: 'TaskList',
  components: {
    [Link.name]: Link,
    [Button.name]: Button,
    CTable
  },
  filters: {
    formatterTime(val) {
      if (!val) return '-';
      return val.split('T')[0];
    }
  },
  data() {
    return {
      tableData: [],
      headers: [
        {
          label: '序号',
          type: 'index',
          width: 80
        },
        {
          label: '任务名称',
          prop: 'taskName',
          colType: 'column',
          slotName: 'link'
        },
        {
          label: '提交时间',
          prop: 'createTime',
          colType: 'column',
          slotName: 'time'
        },
        {
          label: '进程',
          colType: 'column',
          slotName: 'btns',
          align: 'center',
          prop: 'name',
          btns: [
            {
              label: '办结',
              size: 'small',
              type: 'text',
              click: (row, rowIndex, btnIndex) => {
                console.log(row, rowIndex, btnIndex);
              }
            },
            {
              label: '已办结',
              size: 'small',
              type: 'text',
              hidden: true
            }
          ]
        }
      ],
      loading: true // 列表是否加载中
    };
  },
  //监听属性 类似于data概念
  computed: {
    ...mapGetters(['staffName'])
  },
  //监控data中的数据变化
  watch: {},

  methods: {
    /**
     * 获取任务列表
     */
    manageGetTaskList() {
      manageGetTaskList()
        .then(({ data }) => {
          this.tableData = data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    /**
     * 办结
     */
    onCompelete(row) {
      if (row.taskName) {
        this.$confirm(
          '确定办结该任务？<br>办结后未提交学校单位将无法提交',
          '提醒',
          {
            confirmButtonText: '确认',
            cancelButtonText: '取消',
            center: true,
            showClose: false,
            dangerouslyUseHTMLString: true
          }
        ).then(() => {
          completeTask({ taskName: row.taskName }).then(() => {
            this.manageGetTaskList();
            this.$message({
              type: 'success',
              message: '操作成功!'
            });
          });
        });
      } else {
        this.$message({
          message: '任务名称不存在，请联系管理员',
          type: 'warning'
        });
      }
    },
    /**
     * 新增任务
     */
    onClickNewTask() {
      console.log('add');
      this.$prompt('', '新建任务', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        inputPattern: /^.{1,20}$/,
        inputErrorMessage: '任务名称需在1-20位',
        inputPlaceholder: '请输入任务名称',
        center: true,
        showClose: false
      }).then(({ value }) => {
        isHaveTaskName({ taskName: value }).then(({ data }) => {
          // data:true用户名存在,不可新增
          // data:false用户名不存在,可新增
          if (data) {
            this.$message({
              message: '任务名称已存在，请重新填写',
              type: 'warning'
            });
          } else {
            this.$router.push({
              path: '/new-task/index',
              query: {
                taskName: value
              }
            });
          }
        });
      });
    },
    /**
     * 跳转到任务详情
     */
    onJump(row) {
      if (row.status === '0') {
        // 未办结
        this.$router.push({
          path: '/task-detial/not-complete',
          query: {
            taskName: row.taskName
          }
        });
      } else if (row.status === '1') {
        // 已办结
        this.$router.push({
          path: '/task-detial/complete',
          query: {
            taskName: row.taskName
          }
        });
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.manageGetTaskList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {}
};
</script>
