    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .btn-blue {
    width: 88px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background: #ffffff;
    border-radius: 4px;
    border: 1px solid #0076f6;
    color: #0076f6;
    padding: 0;
    :hover {
      color: #0076f6;
    }
  }
  .btn-red {
    display: inline-block;
    width: 88px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border-radius: 4px;
    border: 1px solid transparent;
